* {
  font-family: "Jost", sans-serif;
  list-style: none;
  color: #1d1d1d;
}

a {
  color: #1d1d1d;
}
a:hover {
  color: #1d1d1d;
}
p {
  font-size: 1.2rem;
  margin: 0;
}

h1 {
  font-size: 6rem;
  font-weight: bolder;
  letter-spacing: -5px;
}

h2{
  padding: 1rem 0;
}
body {
  max-width: 60vw;
  margin: auto;
  color: #1d1d1d;
}

/* Header  */

.navbar {
  font-size: 1.2rem;
}

.logo {
  font-family: "Splash", cursive;
  font-size: 3rem;
  color: #1d1d1d;
  text-decoration: none;
}

.logo:hover {
  color: #1d1d1d;
}

/* Hero  */

.hero p{
  opacity: 0.64;
  font-size: 1.7rem;
  transition: opacity 0.2s ease-in-out;
  letter-spacing: -2px;
}


/* Responsive styling using media query */
@media (min-width: 768px) {
  .profile-image {
    max-width: 100%; /* Adjust as needed for your layout */
  }
}


/* Projects  */
.project li {
  opacity: 0.64;
  transition: opacity 0.2s ease-in-out;
}
.project li:hover {
  opacity: 1;
}

/* Skills  */

.skills .card{
  background-color: transparent !important;
}

/* Contact  */

.contact .form-control:focus {
  color: #1d1d1d;
  background-color: #fff;
  outline: 0;
  border-color:#ced4da;
  box-shadow: none;
}
/* // Small devices (landscape phones, 576px and up) */
@media (max-width: 576px) {
  body {
    max-width: 100vw;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (max-width: 768px) {
  body {
    max-width: 100vw;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (max-width: 992px) {
  body {
    max-width: 100vw;
    padding: 40px;
  }
}
